@import "../../../Assets/colorPallete/colorPallete.scss";
@import "../../../Assets/fontFamily/nunitoSans.scss";

.root {
  margin: 10px 0;
  .title {
    color: $color-12;
    margin-bottom: 0;
  }
  .content {
    padding: 0;
    padding-top: 20px;
    .tableHead {
      .tableCell {
        color: $color-12;
      }
    }
    .tableBody {
      .tableCell {
        color: $color-12;
      }
      .tableCellFlex {
        display: flex;
        color: $color-12;
        justify-content: flex-start;
        align-items: center;
      }
    }
  }
}
