@import "../../../Assets/colorPallete/colorPallete.scss";
@import "../../../Assets/fontFamily/nunitoSans.scss";

.root {
  margin: 10px 0;
  .title {
    color: #1e2071;
    margin-bottom: 0;
  }
  .content {
    padding: 0;
    padding-top: 20px;
    .tableHead {
      .tableCell {
        color: #1e2071;
      }
    }
    .tableBody {
      .tableCell {
        color: #1e2071;
      }
      .tableCellFlex {
        display: flex;
        color: #1e2071;
        justify-content: flex-start;
        align-items: center;
      }
    }
  }
  .componentTextGroup {
    margin-right: 80px;
    .componentTextGroupLabel {
      color: #9fa1c3;
    }
    .componentTextGroupContent {
      color: #1e2071;
      font-weight: normal;
    }
  }
}
