@import "../../../../Assets/colorPallete/colorPallete.scss";
@import "../../../../Assets/fontFamily/nunitoSans.scss";
.root {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: auto;
    .content {
        padding: 20px;
    }
    .btnGroup {
        display: flex;
        justify-content: flex-end;
        .btnRed {
            background: $color-10;
            color: $color-1;
            margin: 0 10px;
        }
        .btnGreen {
            background: $color-9;
            color: $color-1;
            margin: 0 10px;
            .btnSubTitle {
                font-size: x-small;
            }
            .btnSaveChildren {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                p {
                    margin: 0;
                }
            }
        }
    }
    .footerPage {
        display: flex;
        justify-content: flex-end;
        .footerText {
            color: $color-3;
            margin-right: 20px;
        }
    }
}

.dialogContainer {
    .btnGroup {
        display: flex;
        justify-content: center;
        .btnRed {
            background: $color-10;
            color: $color-1;
            margin: 0 10px;
        }
        .btnGreen {
            background: $color-9;
            color: $color-1;
            margin: 0 10px;
        }
    }
}