@import "styles/color.scss";
.root {
  .title {
    color: #1e2071;
    margin-bottom: 0;
  }
  .content {
    width: 100%;
    padding: 20px;
    padding-top: 0;
    .componentRadioGroup {
      .componentRadioLabel {
        &Disable{
          color:#9ea3ac;
          &:hover{
            cursor: pointer;
          }
        }
        color: #1e2071;
        font-weight: bold;
        &:hover{
          cursor: pointer;
        }
      }
      .componentRadioButton {
        color: #1e2071;
        &Disable{
          color:#9ea3ac;
          &:hover{
            cursor: pointer;
          }
        }
      }
      .componentRadioInputLabel {
        color: #1e2071;
        margin-right: 15px;
        font-weight: normal;
        &Disable{
          margin-right: 15px;
          font-weight: normal;
          color:#9ea3ac;
          &:hover{
            cursor: pointer;
          }
        }
      }
      &:hover{
        cursor: pointer;
      }

    }
  }
  .componentTextGroup {
    margin-right: 80px;
    .componentTextGroupLabel {
      color: #9ea3ac;
    }
    .componentTextGroupContent {
      color: #1e2071;
      font-weight: normal;
    }
  }
}
