.star {
    display: flex;
    flex-direction: row;
    height: auto;
    width: 100%;
    align-items: center;

    .facilityAssignment {
        flex: 1;
        display: flex;
        flex-direction: row;
        align-items: center;
        > img {
            margin-right: 0.5em;
            width: 20px;
            height: 20px;
        }
        > label {
            font-size: 0.9vw;
            color: rgba(31, 58, 147, 0.5);
        }
    }

    > div:nth-child(2) {
        flex: 1;
        height: auto;
    }

    .medicalPartnerStatus {
        flex: 2;
        > label {
            font-size: 0.9vw;
            color: rgba(31, 58, 147, 0.5);
        }
    }
}

.secondLayer {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;

    .radioButtons {
        display: flex;
        flex-direction: column;
        height: auto;
        width: 100%;
        align-items: center;

        .formControl {
            width: 100%;
            height: auto;

            .radioGroup {
                margin-left: 3em;
                display: flex;
                width: 100%;
                height: auto;

                .none {
                    margin-right: 6em;
                    width: auto;
                    height: auto;
                }

                .assigned {
                    margin-left: 6em;
                    width: auto;
                    height: auto;
                }
            }
        }

        .checkboxes {
            width: auto;
            height: auto;

            .formControl {
                margin-left: 3em;
                .checkBoxGroup {
                    .specialAccommodation {
                        display: flex;
                        width: 100%;
                        flex-direction: row;
                        position: relative;

                        .inputHide {
                            display: none;
                        }
                        .exclusiveTo {
                            position: absolute;
                            margin-left: 16em;
                            margin-top: 0.5em;
                            display: flex;
                            flex-direction: row;
                            width: 100%;

                            .inputfield {
                                transform: translateX(8%);
                            }
                            > label {
                                font-size: 15px;
                                width: 40%;
                                font-weight: 595;
                                margin-top: 0.2em;
                                margin-left: 0.2em;
                            }
                        }
                    }
                }
            }
        }
    }

    .medicalPartnerStatusRadioButtons {
        width: 100%;
        height: auto;

        .formControl {
            .radioGroup {
                .onHold {
                    display: flex;
                    flex-direction: row;
                    width: 100%;
                    height: auto;
                    .onHoldInput {
                        display: flex;
                        flex-direction: row;

                        .inputfield {
                            transform: translateX(8%);
                            margin-top: 0.55em;
                        }

                        > label {
                            margin-top: 0.7em;
                            font-size: 15px;
                            width: 40%;
                            font-weight: 595;
                            margin-left: 0.2em;
                        }
                    }
                    .onHoldHide {
                        display: none;
                    }
                }
            }
        }
    }
}
