@import "../../../../Assets/colorPallete/colorPallete.scss";
@import "../../../../Assets/fontFamily/nunitoSans.scss";

.root {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: nunitoSansRegular;
    padding-left: 5.8em;

  th:nth-child(1){
    width: 10%;
  }

  th:nth-child(2){
    width: 17%;
  }
  
    .container {
        margin-top: 3em;
        margin-right: 0.5em;
        width: 100%;
        height: 60%;
        display: grid;
        flex-direction: row;
        grid-template-rows: 0.3fr 1fr 0.2fr 0.4fr;

        > div:nth-child(1) {
            background-color: $color-4;
            width: 100%;
            height: auto;
            display: flex;
            justify-content: flex-end;
            align-items: flex-end;
            padding-bottom: 0.4em;
            padding-right: 3em;
            .button {
                margin-top: 2em;
                background-color: $color-9;
                color: white;
                text-transform: initial;
                padding-left: 1.5em;
                padding-right: 1.5em;
                font-size: 0.8vw;
            }
        }

        > div:nth-child(2) {
            background-color: $color-4;
            width: 100%;
            height: auto;
            display: flex;
        }
        > div:nth-child(3) {
            background-color: $color-4;
            width: 100%;
            height: auto;
            display: flex;
        }

        > div:nth-child(3) {
            background-color: transparent;
            width: 100%;
            height: auto;
            display: flex;
            justify-content: flex-end;
            align-items: flex-start;
            padding-top: 0.4em;
            padding-right: 0.5em;
            .button {
                background-color: $color-10;
                color: white;
                text-transform: initial;
                padding-left: 2.5em;
                padding-right: 2.5em;
                font-size: 1vw;
            }
            .btnCancel {
                background-color: $color-10;
                color: white;
                text-transform: initial;
                padding-left: 2.5em;
                padding-right: 2.5em;
                font-size: 1vw;
            }
            .btnSave {
                background-color: $color-9;
                color: white;
                text-transform: initial;
                padding-left: 2.5em;
                padding-right: 2.5em;
                margin-left: 1.5em;
                font-size: 1vw;
            }
        }

        .closeButton {
            height: auto;
            display: flex;
            justify-content: flex-end;
            background-color: $color-10;
            color: white;
            text-transform: initial;
            padding-left: 2.5em;
            padding-right: 2.5em;
            font-size: 1vw;
            float: right;
            margin-top: 10px;
        }
    }
}
