@import "../../../Assets/colorPallete/colorPallete.scss";
@import "../../../Assets/fontFamily/nunitoSans.scss";

.root {
  margin: 10px 0;

  .ratingsGroup {
    display: flex;
    justify-content: center;
  }
  .circularButton {
    background-color: $color-1;
    border: solid 2px $color-4;
    border-radius: 50%;
    height: 67px;
    width: 67px;
    margin: 0 5px;
    &:hover {
      cursor: pointer;
    }
    &:focus{
      
      outline:none;
     }
    .circularButtonContent {
      display: flex;
      justify-content: center;
      align-items: center;
      border: none;
    }
  }

  .headerContainer {
    display: flex;
    justify-content: space-between;
    .title {
      color: $color-12;
      margin-bottom: 0;
    }
    .subTitle {
      color: $color-12;
      font-weight: normal;
      font-style: italic;
      margin-top: 0;
    }
    .ratingContainer {
      display: flex;
      align-items: flex-end;
      .ratingLabel {
        color: $color-12;
        font-weight: bold;
        .ratingScore {
          color: $color-9;
          font-weight: bold;
          font-size: 1.7rem;
        }
      }
    }
  }
  .content {
    padding: 0;
    padding-top: 20px;

    .table {
      height: 200px;
      overflow-y: scroll;
    }

    .tableHead {
      .tableCell {
        color: $color-12;
      }
    }
    .tableBody {
      height: 100px;
      overflow-y: scroll;
      .tableCell {
        color: $color-12;
        .tableCellTitle {
          color: $color-9;
          font-weight: bold;
          margin: 0;
        }
        .tableCellText {
          margin: 0;
          color: $color-12;
          font-weight: normal;
          font-size: 0.9rem;
        }
      }
      .tableCellFlex {
        display: flex;
        color: $color-12;
        justify-content: flex-start;
        align-items: center;
      }
    }
  }
}
