@import "../../../Assets/colorPallete/colorPallete.scss";
@import "../../../Assets/fontFamily/nunitoSans.scss";

.root {
  .title {
    color: $color-12;
    margin-bottom: 0;
  }
  .content {
    width: 100%;
    padding: 20px;
    padding-top: 0;
    .radioGroup {
      display: flex;
      .radioChoiceGroup {
        display: flex;
        justify-content: space-between;
        width: 200px;
        margin-right: 20px;
        .radioLabelActive {
          font-weight: normal;
          color: $color-12;
          &:hover {
            cursor: pointer;
          }
        }
        .radioLabelDisable {
          font-weight: normal;
          color: $color-4;
          &:hover {
            cursor: pointer;
          }
        }
        .radioField {
          color: $color-12;
        }
        .radioFieldDisable {
          color: $color-4;
        }
      }
      .inputGroup {
        display: flex;
        align-items: center;
        color: $color-12;
        .inputLabelActive {
          margin-right: 10px;
          font-weight: normal;
          color: $color-12;
          &:hover {
            cursor: pointer;
          }
        }
        .inputLabelDisable {
          margin-right: 10px;
          font-weight: normal;
          color: $color-4;
          &:hover {
            cursor: pointer;
          }
        }
      }
    }
  }
}
