@import "../../../../Assets/colorPallete/colorPallete.scss";
@import "../../../../Assets/fontFamily/nunitoSans.scss";
.root {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: auto;
    .content {
        padding: 20px;
    }
    .btnGroup {
        display: flex;
        justify-content: flex-end;
        .btnCancel {
            background: #fd5151;
            color: #f5f7fb;
            margin: 0 10px;
        }
        .btnReturn {
            background: #3ab77d;
            color: #f5f7fb;
            margin: 0 10px;
        }
        .btnSave {
            background: #3ab77d;
            color: #f5f7fb;
            margin: 0 10px;
            .btnSaveChildren {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                p {
                    margin: 0;
                }
            }
        }
        .btnNext {
            background: #3ab77d;
            color: #f5f7fb;
            margin: 0 10px;
        }
    }
    .footerPage {
        display: flex;
        justify-content: flex-end;
        .footerText {
            color: #151c1b;
            margin-right: 20px;
        }
    }
}

.dialogContainer {
    .btnGroup {
        display: flex;
        justify-content: center;
        .btnCancel {
            background: #fd5151;
            color: #f5f7fb;
            margin: 0 10px;
        }
        .btnSave {
            background: #3ab77d;
            color: #f5f7fb;
            margin: 0 10px;
        }
    }
}