@import "../../Assets/colorPallete/colorPallete.scss";
@import "../../Assets/fontFamily/nunitoSans.scss";

.InputFields {
    width: 80%;
    height: 25px;
    border-radius: 5px;
    border: 1px solid gray;
    padding-left: 0.5em;

    background-color: white;

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }

    /* Firefox */
    input[type="number"] {
        -moz-appearance: textfield;
    }
}

.InputFieldsMask {
    width: 80%;
    height: 25px;
    border-radius: 5px;
    border: 1px solid gray;
    padding-left: 0.5em;
    font-size: 16px;
    background-color: white;

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }

    /* Firefox */
    input[type="number"] {
        -moz-appearance: textfield;
    }
}

.InputFieldsErrorMask {
    width: 80%;
    height: 25px;
    border-radius: 5px;
    border: 1px solid red;
    padding-left: 0.5em;
    font-size: 16px;
    background-color: white;

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }

    /* Firefox */
    input[type="number"] {
        -moz-appearance: textfield;
    }
}

.InputFieldsError {
    width: 80%;
    height: 25px;
    border-radius: 5px;
    border: 1px solid red;
    padding-left: 0.5em;
    color: $color-12;
    background-color: white;

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }

    /* Firefox */
    input[type="number"] {
        -moz-appearance: textfield;
    }
}

.error {
    font-size: 12px;
    margin-left: 0.5em;
    margin-top: 0.5em;
    text-transform: initial;
    color: red;
}

.dropDownwithLabel {
    width: auto;
    height: auto;
