@import "styles/color.scss";
.root {
  .title {
    color: #1e2071;
  }
  .content {
    padding: 0;
    .tableHead {
      .tableCell {
        color: #1e2071;
      }
    }
    .tableBody {
      .tableCell {
        color: #1e2071;
      }
      .tableCellFlex {
        display: flex;
        color: #1e2071;
        justify-content: space-between;
      }
    }
  }
  .componentTextGroup {
    margin-right: 80px;
    .componentTextGroupLabel {
      color: #9fa1c3;
    }
    .componentTextGroupContent {
      color: $color-1;
      font-weight: normal;
    }
  }
}
